body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8; }

p {
  color: #000000;
  font-size: 2rem; }

a {
  text-decoration: none; }

img,
picture {
  width: 100%;
  display: block; }

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

.logo {
  width: 70px;
  align-items: center; }

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

.contenido-centrado {
  max-width: 800px; }

.esquinado {
  border-radius: 1rem; }

.elemento:hover {
  -webkit-box-shadow: 0px 0px 9px 2px #f9b233;
  -moz-box-shadow: 0px 0px 9px 2px #f9b233;
  box-shadow: 0px 0px 9px 2px #f9b233; }

.barra {
  display: flex;
  flex-direction: column;
  background-color: #F9B233;
  align-items: center;
  padding: 1rem; }
  .barra .logo {
    width: 20rem;
    padding-bottom: 7.5px; }
  .barra a {
    text-decoration: none;
    color: white; }
    .barra a:hover {
      color: #401616; }
  .barra ul {
    list-style: none;
    padding: 1rem;
    margin: auto;
    display: flex;
    gap: 2rem;
    justify-content: center;
    font-size: 1.5rem; }
  .barra .input {
    -webkit-box-shadow: 4px 7px 9px -7px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 4px 7px 9px -7px rgba(0, 0, 0, 0.75);
    box-shadow: 4px 7px 9px -7px rgba(0, 0, 0, 0.75);
    width: 80%; }
  @media (min-width: 720px) {
    .barra {
      flex-direction: row;
      justify-content: center;
      align-items: center;
      gap: 4rem; }
      .barra .logo {
        flex-basis: calc(14%); }
      .barra .input {
        flex-basis: calc(40%); }
      .barra ul {
        align-items: center;
        gap: 2rem; } }

.producto {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 1rem; }
  .producto a {
    text-decoration: none; }
  .producto img {
    padding: 5px;
    padding-bottom: 0px; }
  .producto:hover {
    -webkit-box-shadow: 0px 0px 9px 2px #f9b233;
    -moz-box-shadow: 0px 0px 9px 2px #f9b233;
    box-shadow: 0px 0px 9px 2px #f9b233; }
  .producto .precio-nombre {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .producto .nombre {
    text-transform: uppercase;
    font-size: 1.2rem; }
  .producto .precio {
    font-weight: bold;
    font-size: 1.5rem; }

.contenedor-productos {
  padding: .75rem;
  -webkit-box-shadow: 0px 0px 44px -22px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 44px -22px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 44px -22px rgba(0, 0, 0, 0.75);
  margin-top: 5rem;
  margin-bottom: 5rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr); }
  @media (min-width: 720px) {
    .contenedor-productos {
      display: grid;
      gap: 3rem;
      grid-template-columns: repeat(3, 1fr); } }
  @media (min-width: 1024px) {
    .contenedor-productos {
      display: grid;
      gap: 4rem;
      grid-template-columns: repeat(4, 1fr); } }

.carousel-inner {
  padding: 0; }

.img {
  height: 20rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  overflow: visible; }
  @media (min-width: 720px) {
    .img {
      height: 30rem; } }

.img1 {
  background-image: url(../img/banner1.webp); }

.img2 {
  background-image: url(../img/banner2.webp); }

.img3 {
  background-image: url(../img/grafico1.webp); }

.img4 {
  background-image: url(../img/grafico2.webp); }

.contenedor-producto {
  display: flex;
  align-items: center;
  width: 90%;
  margin: 5rem;
  gap: 2rem; }
  .contenedor-producto .imagen-producto {
    flex-basis: calc(50%);
    border: 1px solid black; }
  .contenedor-producto .detalles {
    flex-basis: calc(50%);
    text-align: center;
    display: grid;
    gap: 2rem;
    grid-template-rows: repeat(2, 1fr); }
  .contenedor-producto .detalles-1 {
    border-bottom: .2px solid black; }
    .contenedor-producto .detalles-1 .precio {
      font-size: 3rem;
      color: #4a9400; }
  .contenedor-producto .detalles-2 {
    text-align: left; }
    .contenedor-producto .detalles-2 p {
      font-size: 1.4rem; }

.footer {
  margin-top: .75rem;
  background-color: #291717;
  display: flex;
  flex-direction: column;
  gap: 2rem; }
  .footer p {
    text-align: center;
    color: #aa9191;
    margin: 0;
    padding-bottom: 1rem;
    font-size: 1.7rem; }
  .footer a {
    text-decoration: none;
    color: #aa9191; }
    .footer a:hover {
      color: #ffc476; }
  .footer ul {
    list-style: none;
    padding: 1rem;
    margin: auto;
    display: flex;
    gap: 2rem;
    justify-content: center;
    font-size: 1.5rem; }
